.home{width:100%;height:700px;display:flex;align-items:center;justify-content:space-between;gap:20px;color:#eca049;padding:0 30px}.home .imageContainer{width:100%;height:100%;display:flex;flex-flow:column;justify-content:center}.home .imageContainerUp{height:400px;display:flex;flex-flow:column;justify-content:center;color:#eca049;text-align:center;font-size:18px}.home .imageContainerUp p{margin:5px}.home .imageContainerDown{width:100%;height:50px;display:flex;justify-content:center;align-items:center;padding:10px 0 30px 0}.home .image{width:200px;height:200px;margin:15px auto;border:1px solid #eca049}.home .info{width:100%;height:100%;display:flex;flex-flow:column;justify-content:center}.home .info .infoContainer{width:100%;height:450px;padding:15px;display:flex;flex-flow:column;justify-content:center}.home .info p{font-family:"VT323",sans-serif;font-size:18px;line-height:21px;margin:0;text-align:justify}.home .info p:after{content:"â– ";animation:cursorbox 1s infinite;animation-timing-function:step-end;margin-left:1px}.home .line::after{display:block;height:1px;width:60%;margin:10px auto;content:"";background-color:#eca049;box-shadow:0 0 5px #eca049;border-radius:5px}@keyframes cursorbox{0%{visibility:hidden}50%{visibility:visible}100%{visibility:hidden}}@media(max-width: 768px){.home{width:100%;height:100%;flex-direction:column;padding:60px 30px}}